.content-container {
  max-height: 100vh;
  margin-top: 10%;
}
.hero,
.hero:before {
  background: 50% 50% / cover;
}
.hero:before {
  position: absolute;
  bottom: 0;
  content: "";
  left: 0;
  right: 0;
  top: 50px;
  display: block;
  background-image: url(/static/media/Garden.bb7a5a72.png);
  opacity: 0.45;
  background-size: cover;
  transform-origin: center center 0;
  transform: translateZ(-1px) scale(2);
  z-index: -1;
  height: 100vh;
}
@media (max-width: 767.98px) {
  .hero:before {
    height: 50vh;
  }
}
